import React from "react";
import {useForm} from "react-hook-form";
import {ErrorMessage} from "@hookform/error-message"
import {errorToast, getCookieValue, ipAddress, successToast, tokenName} from "../../../utils/Common";
import axios from "axios";

const GroupTypeMappingForm = ({agent, groupTypes, readAgentDetails}) => {
  const getDefaultValues = () => {
    // console.log(agent)
    if (agent && agent.GroupType) {
      return {GroupTypeId: agent.GroupType};
    } else {
      return {};
    }
  };

  const {
    register,
    handleSubmit,
    formState: {errors}
  } = useForm({
    defaultValues: getDefaultValues()
  });

  async function onSubmit(data) {
    try {
      data["Id"] = agent?.UserId;
      let token = getCookieValue(tokenName);
      let url = ipAddress + "admin/agent-group-mapping";

      let res = await axios.post(url, data, {
        headers: {'Authorization': `Bearer ${token}`}
      });
      // console.log(res);
      // console.log(res.data);

      if (res.status === 200) {
        if (res.data.error) {
          errorToast(res.data.message);
        } else {
          successToast(res.data.message);
          readAgentDetails(agent?.AspNetUserId);
        }
      }
    } catch (e) {
      errorToast(e.message)
    }
  }

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <div className="mb-3">
        <label htmlFor="GroupTypeId">Group Type:</label>
        <select name="GroupTypeId" id="GroupTypeId" className="form-select"
                {...register('GroupTypeId', {required: "This is a required field."})}>
          <option value="">Please Select</option>
          {groupTypes.map(x =>
            <option value={x?.GroupName} key={x?.GroupID}>{x?.Description}</option>
          )}
        </select>

        <ErrorMessage errors={errors} name="GroupTypeId"
                      render={({message}) => <p className="text-danger">{message}</p>}
        />
      </div>

      <button className="btn bg-btn w-100 text-white">Update</button>
    </form>
  )
}

export default GroupTypeMappingForm;
