import React from "react";
import {useForm} from "react-hook-form";
import {ErrorMessage} from "@hookform/error-message"
import {errorToast, getCookieValue, ipAddress, successToast, tokenName} from "../../../utils/Common";
import axios from "axios";

const SalesMappingForm = ({agent, salesUsers, readAgentDetails}) => {
  // console.log(agent);
  // console.log(agent.SalesExecutiveId);
  // console.log(salesUsers);

  const getDefaultValues = () => {
    if (agent && agent.SalesExecutiveId && agent.SalesExecutiveId.length > 0) {
      return {SalesExecutiveId: agent.SalesExecutiveId};
    } else {
      return {};
    }
  };

  const {
    register,
    handleSubmit,
    formState: {errors}
  } = useForm({
    // defaultValues: getDefaultValues()
  });

  async function onSubmit(data) {
    try {
      data["Id"] = agent?.UserId;
      let token = getCookieValue(tokenName);
      let url = ipAddress + "admin/agent-sales-mapping";

      let res = await axios.post(url, data, {
        headers: {'Authorization': `Bearer ${token}`}
      });

      if (res.status === 200) {
        if (res.data.error) {
          errorToast(res.data.message);
        } else {
          successToast(res.data.message);
          readAgentDetails(agent?.AspNetUserId);
        }
      }
    } catch (e) {
      errorToast(e.message)
    }
  }

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <div className="mb-3">
        <label htmlFor="SalesExecutiveId">Sales Executive Id Mapping:</label>
        <select name="SalesExecutiveId" id="SalesExecutiveId" className="form-select"
                {...register('SalesExecutiveId', {required: "This is a required field."})}>
          <option value="">Please Select</option>
          {salesUsers.map(x =>
            <option value={x?.Id} key={x?.Id}>
              {x?.FirstName} {x?.LastName} ({x?.Email})
            </option>
          )}
        </select>

        <ErrorMessage errors={errors} name="SalesExecutiveId"
                      render={({message}) => <p className="text-danger">{message}</p>}
        />
      </div>

      <div className="mb-3">
        <label htmlFor="Remarks">Remarks:</label>
        <textarea name="Remarks" id="Remarks" className="form-control"
                  {...register('Remarks', {
                    required: "This is a required field.",
                    validate: value => !/<|>/.test(value) || 'Remarks cannot contain "<" or ">"'
                  })}
        />

        <ErrorMessage errors={errors} name="Remarks"
                      render={({message}) => <p className="text-danger">{message}</p>}
        />
      </div>

      <button className="btn bg-btn w-100 text-white">Update</button>
    </form>
  )
}
export default SalesMappingForm;
