import React from "react";
import {useForm} from "react-hook-form";
import {ErrorMessage} from "@hookform/error-message"
import {errorToast, getCookieValue, ipAddress, successToast, tokenName} from "../../../utils/Common";
import axios from "axios";

const PaymentTypeMappingForm = ({agent, paymentTypes, readAgentDetails}) => {
  // console.log("***")
  // console.log(agent?.AspNetUserId)

  const getDefaultValues = () => {
    if (agent && agent.PaymentType) {
      return {PaymentType: agent.PaymentType};
    } else {
      return {};
    }
  };

  const {
    register,
    handleSubmit,
    formState: {errors}
  } = useForm({
    defaultValues: getDefaultValues()
  });

  async function onSubmit(data) {
    try {
      // console.log(data?.AspNetUserId);
      data["Id"] = agent?.UserId;
      let token = getCookieValue(tokenName);
      let url = ipAddress + "admin/agent-payment-mapping";

      let res = await axios.post(url, data, {
        headers: {'Authorization': `Bearer ${token}`}
      });
      // console.log(res.data);

      if (res.status === 200) {
        if (res.data.error) {
          errorToast(res.data.message);
        } else {
          successToast(res.data.message);
          readAgentDetails(agent?.AspNetUserId);
        }
      }
    } catch (e) {
      errorToast(e.message)
    }
  }

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      {/* Payment Type */}
      <div className="mb-3">
        <label htmlFor="PaymentType">Payment Type:</label>
        <select name="PaymentType" id="PaymentType" className="form-select"
                {...register('PaymentType', {required: "This is a required field."})}>
          <option value="">Please Select</option>
          {paymentTypes && paymentTypes?.map(x => <option value={x} key={x}>{x}</option>)}
        </select>

        <ErrorMessage errors={errors} name="PaymentType"
                      render={({message}) => <p className="text-danger">{message}</p>}
        />
      </div>

      {/* Remarks */}
      <div className="mb-3">
        <label htmlFor="Remarks">Remarks:</label>
        <textarea name="Remarks" id="Remarks" className="form-control"
                  {...register('Remarks', {
                    required: "This is a required field.",
                    validate: value => !/<|>/.test(value) || 'Remarks cannot contain "<" or ">"'
                  })}
        />

        <ErrorMessage errors={errors} name="Remarks"
                      render={({message}) => <p className="text-danger">{message}</p>}
        />
      </div>

      <button className="btn bg-btn w-100 text-white">Update</button>
    </form>
  )
}

export default PaymentTypeMappingForm;
